body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header__appbar {
    width: 100vw;
    height: 72px;
    display: flex;
    background-color: #2e2e2e;
    -webkit-transition: .6s;
    transition: .6s;
}
    .header__appbar.\--sticky {
        position: fixed;
        left: 0;
        top: 0;
    }

.appbar__department {
    display: flex;
    margin: auto 72px auto 72px;
}
    .appbar__department > div > button > span {
        color: #ffffff;
    }
.appbar__department > li {
    width: 72px;
    height: 32px;
    margin: 17px 15px 0 15px;
    list-style: none;
}
.department--mobile {
    text-decoration: none;
    color: #ffffff;
}

@media (max-width: 1265px) {
    .MuiPaper-root-23 {
        position: fixed !important;
        top: 70px !important;
    }
}


@media (max-width: 1265px) {
    .appbar__department > li {
        display: none;
    }
    .department--mobile {
        display: block;
        margin: 0 auto;
    }
}


.appbar__department > li > a {
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    color: #ffffff;
    -webkit-transition: .3s;
    transition: .3s;
}
.appbar__department > li > a:hover {
    color: #6f6f6f;
}

.appbar__title {
    width: 176px;
    height: 29px;
    margin: 23px 10px 20px 31px;
}

.appbar__title > a {
    text-decoration: none;
    color: #f62f5e;
    font-stretch: normal;
    font-style: normal;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    letter-spacing: 4px;
    line-height: normal;
}
.appbar__icons {
    margin: auto 80px auto auto;
    display: flex;
    border-radius: 50px !important;
}

@media (max-width: 1265px) {
    .appbar__icons {
        display: none;
    }
    .appbar__title {
        margin: 23px 10px 20px 17px;
    }
}

.icon__element {
    margin: auto;
}

.shop__element {
    color: #6f6f6f;
}

.long__menu {
    top: 120px;
}

.header__signInBar {
    background-color: #ffffff;
    width: 100vw;
    height: 45px;
    display: flex;
    align-items: center;
}

.signInBar__item {
    display: block;
    width: 200px;
    margin: auto auto auto 10px;
    text-align: center;
    font-family: "Montserrat", sans-serif;
    color: #1e1e1e;
    text-decoration: none;
    font-size: 15px;
}


.signInBar__item > a {
    margin: 0;
    padding: 0;
    color: #f62f5e;
    text-decoration: none;
}

.header__banner {
    display: block;
    padding: 0;
    margin-left: auto;
    overflow: hidden;
}
@media screen and (max-width: 1265px) {
    .header__banner {
        display: none;
    }
}

.header__container {
    display: flex;
}

.header__pageNumber {
    display: flex;
    justify-content: center;
    text-align: center;
    width: 300px;
    margin: 250px auto 0 auto;
    z-index: 100;
}

.pageNumber__arrow {
    color: #787878;
    margin: 0 5px;
    cursor: pointer;
}

.pageNumber__item {
    width: 20px;
    height: 20px;
    border-radius: 15px;
    margin: 5px;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    background-color: #f2f2f2;
    cursor: pointer;
}

.pageNumber__item:hover {
    box-shadow: 2px 2px 4px 0 #c9c9c9;
}

.main__sidebar {
    position: fixed;
    padding: 20px;
    margin: 0 10px 10px;
    top: 125px;
}


.sidebar__title {
    width: 200px;
    margin: 0;
    padding: 10px 0 10px 0;
    font-family: 'Montserrat', sans-serif;
    text-align: left;
    color: #000000;
    font-size: 22px;
    letter-spacing: 3px;
    border-bottom: 1px outset #4e4e4e;
}

.main__sidebar > ul {
    list-style: none;
    padding-left: 8px;

}
@media (max-width: 1265px) {
    .main__sidebar > ul {
        padding: 15px;
    }
}

.main__sidebar > ul > li > a {
    font-family: 'Montserrat', sans-serif;
    text-align: left;
    font-size: 20px;
    color: #6f6f6f;
    margin: 15px 15px 15px 0;
    text-decoration: none;
    display: block;
    width: 100px;
    -webkit-transition: .3s;
    transition: .3s;
}

.main__sidebar > ul > li:first-child > a {
    margin-top: 35px;
}

.main__sidebar > ul > li > a:hover {
    color: #f62f5e;
}


@media (max-width: 1265px) {
    .main__sidebar {
        top: 150px;
        padding: 0;
        height: auto;
        margin-top: 0;
        margin-left: 20px;
    }
    .main__sidebar > ul {
        list-style: none;
        margin: 0;
        padding: 4px 0 0 8px;
    }

}

main {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 65px;
}

@media (max-width: 1265px) {
    main {
        margin-top: 130px;
    }
}

.main__card {
    background-color: #f2f2f2;
    width: 200px;
    margin: 30px;
    border-radius: 5px;
    height: 430px;
    -webkit-transition: .4s;
    transition: .4s;
    box-shadow: 4px 5px 11px -5px #a1a1a1;
}
.main__card:hover {
    box-shadow: 4px 5px 18px 0 #a1a1a1;
}

.card__price {
    display: block;
    width: 93px;
    border-radius: 15px;
    background-color: #f2f2f2;
    border: 1px solid #f62f5e;
    height: 24px;
    text-align: center;
    margin: 15px auto 0 auto;
    padding: 5px;
    color: #232323;
    -webkit-transition: .4s;
    transition: .4s;
}
.card__price:hover {
    background: #f62f5e;
    cursor: default;
}

.card__name {
    width: 180px;
    margin: 23px auto;
    color: #4e4e4e;
    text-align: center;
}
.card__description {
    width: 180px;
    overflow: hidden;
    height: 75px;
    font-size: 14px;
    color: #4e4e4e;
    margin: 15px auto;
    -webkit-transition: .4s ease-in-out;
    transition: .4s ease-in-out;
    text-align: center;
}


.card__image {
    display: block;
    margin: 25px auto;
    border-radius: 5px;
    -webkit-transition: .3s;
    transition: .3s;
}


.card__button {
    font-family: 'Montserrat', sans-serif;
    width: 93px;
    border-radius: 24px;
    border: none;
    background-color: #f62f5e;
    margin: 30px auto;
    display: block;
    height: 38px;
    color: #232323;
    -webkit-transition: .4s;
    transition: .4s;
}
.card__button:hover {
    background-color: #f2f2f2;
    border: 1px solid #f62f5e;
    cursor: pointer;
}

